import { google } from "googleapis";
import { Bonus, bonuses as fallbackBonuses } from "./data";

/**
 * Google Sheets Service for fetching casino bonus data
 * Similar structure to NestJS service but adapted for Next.js
 */
export class GoogleSheetsService {
  private sheets: any;
  private spreadsheetId: string | undefined;
  private initialized: boolean = false;

  constructor() {
    this.initialize();
  }

  /**
   * Initialize Google Sheets API client
   */
  private initialize() {
    const serviceAccountEmail = process.env.GOOGLE_SERVICE_ACCOUNT_CLIENT_EMAIL;
    const privateKey = process.env.GOOGLE_SERVICE_ACCOUNT_PRIVATE_KEY;

    if (!serviceAccountEmail || !privateKey) {
      console.warn("⚠️ Google Sheets credentials not configured. Google Sheets sync will be disabled.");
      console.warn("Required: GOOGLE_SERVICE_ACCOUNT_CLIENT_EMAIL and GOOGLE_SERVICE_ACCOUNT_PRIVATE_KEY");
      return;
    }

    try {
      // Get credentials from environment variables or file
      const credentials = this.getCredentials();

      // Validate credentials structure
      if (!credentials.client_email || !credentials.private_key) {
        throw new Error("Missing required credential fields: client_email or private_key");
      }

      const auth = new google.auth.GoogleAuth({
        credentials,
        scopes: ["https://www.googleapis.com/auth/spreadsheets.readonly"],
      });

      this.sheets = google.sheets({ version: "v4", auth });
      this.spreadsheetId = process.env.GOOGLE_SHEETS_SPREADSHEET_ID;

      if (!this.spreadsheetId) {
        console.warn("⚠️ GOOGLE_SHEETS_SPREADSHEET_ID is not set. Google Sheets sync will be disabled.");
        return;
      }

      this.initialized = true;
      console.log("✅ Google Sheets service initialized successfully");
    } catch (error: any) {
      console.error("❌ Failed to initialize Google Sheets service:", error);
      
      // Provide specific guidance for OpenSSL errors
      if (error.code === "ERR_OSSL_UNSUPPORTED" || error.message?.includes("DECODER")) {
        console.error("⚠️ OpenSSL decoder error detected. This usually means:");
        console.error("   1. The private key format is incorrect");
        console.error("   2. The private key has encoding issues");
        console.error("   3. Try using the service-account.json file instead");
        console.error("   4. Make sure GOOGLE_SERVICE_ACCOUNT_PRIVATE_KEY has proper newlines (\\n)");
      }
      
      console.error("Error details:", {
        message: error.message,
        code: error.code,
        library: error.library,
        reason: error.reason,
      });
    }
  }

  /**
   * Get Google Sheets service account credentials from environment variables or file
   */
  private getCredentials() {
    // Try to get from environment variables first
    const privateKeyEnv = process.env.GOOGLE_SERVICE_ACCOUNT_PRIVATE_KEY;

    if (privateKeyEnv) {
      try {
        let privateKey = privateKeyEnv;

        // Remove surrounding quotes if present (handle both single and double quotes)
        privateKey = privateKey.trim().replace(/^["']|["']$/g, "");

        // Handle multiple levels of escaping
        // Replace \\n (double escaped) first, then \n (single escaped)
        // Also handle literal string "\\n" that might be in the env var
        privateKey = privateKey
          .replace(/\\\\n/g, "\n") // Handle \\\\n (quadruple escaped)
          .replace(/\\n/g, "\n") // Handle \\n (double escaped) and \n (single escaped)
          .replace(/\\\\r/g, "\r") // Handle \\\\r
          .replace(/\\r/g, "\r"); // Handle \\r

        // Ensure proper line breaks - the key should have actual newlines
        // If it's still a single line, try to detect and fix
        if (!privateKey.includes("\n") && privateKey.includes("BEGIN PRIVATE KEY")) {
          // Try to reconstruct with proper newlines after BEGIN and before END
          privateKey = privateKey
            .replace(/-----BEGIN PRIVATE KEY-----/g, "-----BEGIN PRIVATE KEY-----\n")
            .replace(/-----END PRIVATE KEY-----/g, "\n-----END PRIVATE KEY-----");
        }

        // Clean up any extra whitespace but preserve the key structure
        privateKey = privateKey.trim();

        // Ensure the private key has proper BEGIN/END markers
        if (!privateKey.includes("BEGIN PRIVATE KEY") || !privateKey.includes("END PRIVATE KEY")) {
          throw new Error("Invalid private key format in GOOGLE_SERVICE_ACCOUNT_PRIVATE_KEY");
        }

        // Validate the key structure
        const beginMatch = privateKey.match(/-----BEGIN PRIVATE KEY-----/);
        const endMatch = privateKey.match(/-----END PRIVATE KEY-----/);
        
        if (!beginMatch || !endMatch) {
          throw new Error("Private key missing BEGIN or END markers");
        }

        return {
          type: process.env.GOOGLE_SERVICE_ACCOUNT_TYPE || "service_account",
          project_id: process.env.GOOGLE_SERVICE_ACCOUNT_PROJECT_ID,
          private_key_id: process.env.GOOGLE_SERVICE_ACCOUNT_PRIVATE_KEY_ID,
          private_key: privateKey,
          client_email: process.env.GOOGLE_SERVICE_ACCOUNT_CLIENT_EMAIL,
          client_id: process.env.GOOGLE_SERVICE_ACCOUNT_CLIENT_ID,
          auth_uri:
            process.env.GOOGLE_SERVICE_ACCOUNT_AUTH_URI || "https://accounts.google.com/o/oauth2/auth",
          token_uri:
            process.env.GOOGLE_SERVICE_ACCOUNT_TOKEN_URI || "https://oauth2.googleapis.com/token",
          auth_provider_x509_cert_url:
            process.env.GOOGLE_SERVICE_ACCOUNT_AUTH_PROVIDER_X509_CERT_URL ||
            "https://www.googleapis.com/oauth2/v1/certs",
          client_x509_cert_url: process.env.GOOGLE_SERVICE_ACCOUNT_CLIENT_X509_CERT_URL,
          universe_domain: process.env.GOOGLE_SERVICE_ACCOUNT_UNIVERSE_DOMAIN || "googleapis.com",
        };
      } catch (envError: any) {
        // If env var parsing fails, try to fall back to file
        console.warn("⚠️ Failed to parse credentials from environment variables, trying service-account.json file...");
        console.warn("Error:", envError.message);
      }
    }

    // Fallback: Try to read from service-account.json file
    try {
      const { readFileSync } = require("fs");
      const { join } = require("path");
      const credentialsPath = join(process.cwd(), "service-account.json");
      const credentials = JSON.parse(readFileSync(credentialsPath, "utf-8"));
      console.log("✅ Using credentials from service-account.json file");
      
      // Also clean the private key from the file if needed
      if (credentials.private_key) {
        credentials.private_key = credentials.private_key
          .replace(/\\n/g, "\n")
          .trim();
      }
      
      return credentials;
    } catch (fileError: any) {
      throw new Error(
        `GOOGLE_SERVICE_ACCOUNT_PRIVATE_KEY not set and service-account.json not found. File error: ${fileError.message}`
      );
    }
  }

  /**
   * Fetches casino bonus data from Google Sheets
   * Falls back to static data if API call fails
   */
  async getBonuses(): Promise<Bonus[]> {
    // Check if sync is enabled (optional flag)
    const syncEnabled = process.env.GOOGLE_SHEETS_SYNC_ENABLED !== "false"; // Default to true
    if (!syncEnabled) {
      console.log("⚠️ Google Sheets sync is disabled. Set GOOGLE_SHEETS_SYNC_ENABLED=true to enable.");
      return fallbackBonuses;
    }

    // Validate required configuration
    if (!this.initialized || !this.sheets) {
      console.error("Google Sheets sync failed: Google Sheets service not initialized (check credentials)");
      return fallbackBonuses;
    }

    if (!this.spreadsheetId) {
      console.error("Google Sheets sync failed: GOOGLE_SHEETS_SPREADSHEET_ID is not set");
      return fallbackBonuses;
    }

    // Get sheet name from env or use default (will be updated from metadata if available)
    let sheetName = process.env.GOOGLE_SHEETS_SHEET_NAME || "Sheet1";

    try {
      // Get metadata to find the correct sheet name
      const meta = await this.sheets.spreadsheets.get({ spreadsheetId: this.spreadsheetId });
      
      // Update sheetName from metadata if not set in env
      if (!process.env.GOOGLE_SHEETS_SHEET_NAME) {
        sheetName = meta.data.sheets?.[0]?.properties?.title || "Sheet1";
      }

      // Define range (default: A2:G to skip header row)
      const range = process.env.GOOGLE_SHEETS_RANGE || `${sheetName}!A2:G`;

      // Fetch data from Google Sheets
      const response = await this.sheets.spreadsheets.values.get({
        spreadsheetId: this.spreadsheetId,
        range,
      });

      const rows = response.data.values;

      if (!rows || rows.length === 0) {
        console.warn("No data found in Google Sheet, using fallback data");
        return fallbackBonuses;
      }

      // Transform sheet rows to Bonus objects
      const bonuses = rows.map((row: any[], index: number) => {
        const [brandName, logoUrl, welcomeBonus, bonusDetails, wager, minDeposit, trackingLink] =
          row;

        // Extract actual image URL if it's a Google redirect
        let image = logoUrl;
        if (logoUrl && logoUrl.includes("imgurl=")) {
          try {
            const urlParams = new URLSearchParams(new URL(logoUrl).search);
            image = urlParams.get("imgurl") || logoUrl;
          } catch (e) {
            console.error("Error parsing image URL:", e);
          }
        }

        // Infer tags from description or title
        const tags: string[] = [];
        const textToScan = `${welcomeBonus} ${bonusDetails}`.toLowerCase();
        if (textToScan.includes("free spins") || textToScan.includes("fs")) tags.push("Free Spins");
        if (textToScan.includes("welcome")) tags.push("Welcome Bonus");
        if (textToScan.includes("no deposit")) tags.push("No Deposit");
        if (textToScan.includes("crypto") || textToScan.includes("btc")) tags.push("Crypto");
        if (textToScan.includes("exclusive")) tags.push("Exclusive");

        // Default tags if none found
        if (tags.length === 0) tags.push("Casino Bonus");

        return {
          id: brandName ? brandName.toLowerCase().replace(/\s+/g, "-") : `bonus-${index}`,
          title: brandName || "Unknown Casino",
          description: `${welcomeBonus} ${bonusDetails}`.trim(),
          link: trackingLink || "#",
          image: image || "/placeholder.png",
          tags: tags,
          rating: 5, // Default rating
          terms: "18+. T&Cs apply.",
          wagering: wager || "N/A",
          minDeposit: minDeposit || "N/A",
          maxBonus: "See Details", // Not in sheet
        };
      });

      console.log(`✅ Successfully fetched ${bonuses.length} bonuses from Google Sheet (${sheetName})`);
      return bonuses;
    } catch (error: any) {
      console.error("❌ Error fetching bonuses from Google Sheet:", error);
      console.error("Error details:", {
        message: error.message,
        code: error.code,
        spreadsheetId: this.spreadsheetId,
        sheetName: sheetName, // Now uses the actual variable, not just env var
      });
      console.warn("Falling back to static data");
      return fallbackBonuses;
    }
  }
}

// Singleton instance
let googleSheetsServiceInstance: GoogleSheetsService | null = null;

/**
 * Get or create the Google Sheets service instance
 */
function getGoogleSheetsService(): GoogleSheetsService {
  if (!googleSheetsServiceInstance) {
    googleSheetsServiceInstance = new GoogleSheetsService();
  }
  return googleSheetsServiceInstance;
}

/**
 * Convenience function to fetch bonuses (maintains backward compatibility)
 * Falls back to static data if API call fails
 */
export async function getBonuses(): Promise<Bonus[]> {
  const service = getGoogleSheetsService();
  return service.getBonuses();
}

